  loadPage(credentials: FileCredentials, page: number) {
    return this._http.post('/api/runtime/dfs/v1.0/docviewer/loaddocumentPage', {
      'filePath': credentials.guid,
      'password': credentials.password,
      'page': page
    }, Api.httpOptionsJson);
  }

  rotate(credentials: FileCredentials, angle: number, page: number): Observable<any> {
    let url='/api/runtime/dfs/v1.0/docviewer/rotatedocumentPage';
    // return this._http.post(url, {
    //   'filePath': credentials.guid,
    //   'password': credentials.password,
    //   'pages': [page],
    //   'angle': angle
    // }, Api.httpOptionsJson);
    let body:RotateDocumentPagesRequest=new RotateDocumentPagesRequest;
    body.filePath=credentials.guid;
    body.angle=angle;
    body.pages=[page];

    return this.http.request('Post', url, this.setHeader({ body })).pipe(
      tap(res => { }),
      catchError((error: HttpErrorResponse) => {
        return of(error);
      }));
  }

