import { Component, OnInit, HostListener, Input } from '@angular/core';

@Component({
  selector: 'app-image-viewer',
  templateUrl: './image-viewer.component.html',
  styleUrls: ['./image-viewer.component.css']
})
export class ImageViewerComponent implements OnInit {

  @Input() imageUrl: string;

  constructor() { }

  ngOnInit() {
  }


  index: number = 0;
  style = { transform: '', msTransform: '', oTransform: '', webkitTransform: '' };
  scale: number = 1;
  rotation = 0;
  translateX = 0;
  translateY = 0;
  prevX: number;
  prevY: number;

  zoomIn() {
    this.scale < 4 ? this.scale += 0.1 : 4;
    this.updateStyle();
  }

  zoomOut() {
    this.scale > 0.3 ? this.scale -= 0.1 : 0.3;
    this.scale = Number(this.scale.toFixed(1));
    this.updateStyle();
  }

  zoomReset() {
    this.scale = 1;
    this.updateStyle();
  }

  scrollZoom(evt) {
    evt.deltaY > 0 ? this.zoomOut() : this.zoomIn();
    return false;
  }

  rotateClockwise() {
    this.rotation += 90;
    this.updateStyle();
  }

  rotateCounterClockwise() {
    this.rotation -= 90;
    this.updateStyle();
  }

  onDragStart(evt) {
    this.prevX = evt.clientX;
    this.prevY = evt.clientY;
  }

  onDragOver(evt) {
    this.translateX += (evt.clientX - this.prevX);
    this.translateY += (evt.clientY - this.prevY);
    this.prevX = evt.clientX;
    this.prevY = evt.clientY;
    this.updateStyle();
  }

  // @HostListener('window:keyup.ArrowLeft')
  // preFile() {
  //   if (this.index > 0) {
  //     this.index -= 1;
  //     this.reset();
  //   }
  // }

  // @HostListener('window:keyup.ArrowRight')
  // nextFile() {
  //   if (this.index < this.src.length - 1) {
  //     this.index += 1;
  //     this.reset();
  //   }
  // }

  imageChange(url: string) {
    this.imageUrl = url;
    this.reset();
  }

  reset() {
    this.scale = 1;
    this.rotation = 0;
    this.translateX = 0;
    this.translateY = 0;
    this.updateStyle();
  }

  updateStyle() {
    this.style.transform = `translate(${this.translateX}px, ${this.translateY}px) rotate(${this.rotation}deg) scale(${this.scale})`;
    this.style.msTransform = this.style.transform;
    this.style.webkitTransform = this.style.transform;
    this.style.oTransform = this.style.transform;
  }

}
