import { Injectable, Injector, ComponentFactoryResolver } from '@angular/core';
//import { FileUploadComponent } from './upload/file-upload/file-upload.component';
import { BsModalService, BsModalRef } from '@farris/ui-modal';
//import { UploadFileInfo } from './upload/entity/uploadfileinfo';
import { Observable, of } from 'rxjs';
import { FileListComponent } from './file-list/file-list/file-list.component';
import { FileListService } from './service/file-list.service';
import { MessagerService } from '@farris/ui-messager';
import { CloudprintService, PrintType, FileType, OutputType } from "@gsp-svc/cloudprint";
import { PdfViewOptions } from './pdf-viewer/entitires/pdfviewoptions';
import { AppService } from '@gsp-sys/rtf-common';
import { UUID } from 'angular2-uuid';
//import { UploadLimit } from './upload/entity/uploadlimit';



@Injectable()
export class FileViewerService {
    constructor(private modalService: BsModalService, private componentFactoryResolver: ComponentFactoryResolver, private injector: Injector,
        private fileListService: FileListService, private msgService: MessagerService, private cloudprintService: CloudprintService, private appService: AppService) { }

    dlg: BsModalRef;

    windowWidth: number = window.innerWidth;
    windowHeigth: number = window.innerHeight;

    //fileInfoList: UploadFileInfo[] = [];

    viewerFormFile(formId: string, rootId: string, viewOptions: PdfViewOptions = new PdfViewOptions) {
        return new Promise(resolve => {

            var viewerFactory = this.componentFactoryResolver.resolveComponentFactory(FileListComponent);
            var viewerRef = viewerFactory.create(this.injector);

            viewerRef.instance.rootId = rootId;
            viewerRef.instance.formId = formId;

            this.dlg = this.modalService.show(viewerRef, {
                title: '',
                width: document.scrollingElement.clientWidth - 20,
                height: document.scrollingElement.clientWidth - 20,
                showButtons: false,
                showMaxButton: false,
                resizable: false
            });
            this.dlg.content.viewOptions = viewOptions;
            // this.dlg.content.formId = formId;
            // this.dlg.content.rootId = rootId;
        })
    }

    viewerFileList(metadataIdList: string[], rootId: string, viewOptions: PdfViewOptions = new PdfViewOptions) {
        return new Promise(resolve => {
            var viewerFactory = this.componentFactoryResolver.resolveComponentFactory(FileListComponent);
            var viewerRef = viewerFactory.create(this.injector);

            viewerRef.instance.rootId = rootId;
            viewerRef.instance.metadataIdList = metadataIdList;

            this.dlg = this.modalService.show(viewerRef, {
                title: '',
                width: document.scrollingElement.clientWidth - 20,
                height: document.scrollingElement.clientWidth - 20,

                showButtons: false,
                showMaxButton: false,
                resizable: false,
                opened: () => {
                    setTimeout(() => {
                        this.dlg.dialog.changeDetectorRef['_appRef'].tick()
                    }, 500)
                }
            });
            this.dlg.content.viewOptions = viewOptions;
            // this.dlg.content.metadataIdList = metadataIdList;
            // this.dlg.content.rootId = rootId;
        })
    }


    viewerFormFileWithIndex(formId: string, rootId: string, metadataId: string, viewOptions: PdfViewOptions = new PdfViewOptions) {
        return new Promise(resolve => {

            var viewerFactory = this.componentFactoryResolver.resolveComponentFactory(FileListComponent);
            var viewerRef = viewerFactory.create(this.injector);

            viewerRef.instance.rootId = rootId;
            viewerRef.instance.formId = formId;
            viewerRef.instance.metadataId = metadataId;

            this.dlg = this.modalService.show(viewerRef, {
                title: '',

                width: document.scrollingElement.clientWidth - 20,
                height: document.scrollingElement.clientWidth - 20,
                showButtons: false,
                showMaxButton: false,
                resizable: false,
                opened: () => {
                    setTimeout(() => {
                        this.dlg.dialog.changeDetectorRef['_appRef'].tick()
                    }, 500)
                }
            });
            this.dlg.content.viewOptions = viewOptions;
            // this.dlg.content.formId = formId;
            // this.dlg.content.rootId = rootId;
        })
    }

    viewerFileListWithIndex(metadataIdList: string[], rootId: string, metadataId: string, viewOptions: PdfViewOptions = new PdfViewOptions) {
        return new Promise(resolve => {
            var viewerFactory = this.componentFactoryResolver.resolveComponentFactory(FileListComponent);
            var viewerRef = viewerFactory.create(this.injector);

            viewerRef.instance.rootId = rootId;
            viewerRef.instance.metadataIdList = metadataIdList;
            viewerRef.instance.metadataId = metadataId;

            this.dlg = this.modalService.show(viewerRef, {
                title: '',
                width: document.scrollingElement.clientWidth - 20,
                height: document.scrollingElement.clientWidth - 20,

                showButtons: false,
                showMaxButton: false,
                resizable: false,
                opened: () => {
                    setTimeout(() => {
                        this.dlg.dialog.changeDetectorRef['_appRef'].tick()
                    }, 500)
                }

            });
            this.dlg.content.viewOptions = viewOptions;
            // this.dlg.content.metadataIdList = metadataIdList;
            // this.dlg.content.rootId = rootId;
        })
    }


    mergeAndViewPdfFiles(metadataIdList: string[], rootId: string) {
        this.fileListService.getMergedFile(metadataIdList, rootId).subscribe(res => {
            if (res.error != null) {
                this.msgService.error(res.error.Message);
            } else {
                let options = {
                    OutputType: OutputType.PRINT,
                    FileType: FileType.Pdf,
                    Path: '',
                    DeviceId: '',
                    printType: PrintType.Form
                };
                this.cloudprintService.openRendedViewer4Attachment("tab", res, options);
            }
        })
    }


    viewerFormFileInpage(formId: string, rootId: string, viewOptions: PdfViewOptions = new PdfViewOptions) {
        this.viewerFormFileWithIndexInPage(formId, rootId, null, viewOptions);
    }

    viewerFileListInPage(metadataIdList: string[], rootId: string, viewOptions: PdfViewOptions = new PdfViewOptions) {
        this.viewerFileListWithIndexInPage(metadataIdList, rootId, null, viewOptions);
    }


    viewerFormFileWithIndexInPage(formId: string, rootId: string, metadataId: string, viewOptions: PdfViewOptions = new PdfViewOptions) {
        this.appService.openAppByOptions({
            appType: 'app',
            funcId: '',
            tabName: 'ææ¡£å¨çº¿é¢è§',
            appId: '6ccb476b-f2ba-df8f-6f47-d37d100c56e2',
            appEntrance: 'docviewertab',
            tabId: UUID.UUID(),
            entityParams: {
                formId: formId,
                rootId: rootId,
                metadataId: metadataId,
                viewOptions: viewOptions
            },
        });
    }

    viewerFileListWithIndexInPage(metadataIdList: string[], rootId: string, metadataId: string, viewOptions: PdfViewOptions = new PdfViewOptions) {
        this.appService.openAppByOptions({
            appType: 'app',
            funcId: '',
            tabName: 'ææ¡£å¨çº¿é¢è§',
            appId: '6ccb476b-f2ba-df8f-6f47-d37d100c56e2',
            appEntrance: 'docviewertab',
            tabId: UUID.UUID(),
            entityParams: {
                metadataIdList: metadataIdList,
                rootId: rootId,
                metadataId: metadataId,
                viewOptions: viewOptions
            },
        });
    }

}