import { Component, OnInit, Input } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-ppt-viewer',
  templateUrl: './ppt-viewer.component.html',
  styleUrls: ['./ppt-viewer.component.css']
})
export class PptViewerComponent implements OnInit {

  @Input() pptUrl:string;

  viewUrl: any

  constructor(private sanitizer: DomSanitizer) { }

  ngOnInit() {
    this.viewUrl=this.sanitizer.bypassSecurityTrustResourceUrl(this.pptUrl);
  }

  pptChange(url: string) {
    this.viewUrl = this.sanitizer.bypassSecurityTrustResourceUrl(url);
  }

}
