import { Component, OnInit, Input } from '@angular/core';
// import "/src/assets/suwell_ofdReader.js";

declare var suwell;

@Component({
  selector: 'app-ofd-viewer',
  templateUrl: './ofd-viewer.component.html',
  styleUrls: ['./ofd-viewer.component.css']
})
export class OfdViewerComponent implements OnInit {
  @Input() filePath: string;
  ocx: any;
  constructor() { }

  ngOnInit() {
    this.ocx = suwell.ofdReaderInit("ofdDiv", "100%", "100%");
    this.ocx.setCompsiteVisible("f_open", true);
    this.ocx.openFile(this.filePath, true);
  }

  fileChange(filePath: string) {
    this.filePath = filePath;
    this.ocx.openFile(filePath, true);
  }
}
